@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

/* navbar=========================>> */

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 5rem;
  color: #fff;
}

nav .brand {
  letter-spacing: -5px;
  font-weight: lighter;
  font-size: 3rem;
  font-family: roboto;
  text-decoration: none;
  color: #fff;
}

nav ul {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #000;
  transition: 0.5s cubic-bezier(0, -0.5, 1, -0.5);
  overflow: hidden;
}

nav ul.active {
  visibility: visible;
  opacity: 1;
}

nav ul .parentIndi {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

nav ul .indicator {
  position: absolute;
  background: #fff;
  height: 2px;
  border-radius: 30px;
  transition: 0.5s cubic-bezier(0, -0.5, 1, -0.5);
}

nav ul a {
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: #fff;
  letter-spacing: 3px;
}

.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 3rem;
  height: 2.32rem;
  cursor: pointer;
  z-index: 3;
}

.hamburger span {
  width: 100%;
  height: 3px;
  transition: 0.5s cubic-bezier(0, -0.5, 1, -0.5);
  background: #fff;
  -webkit-transform-origin: left;
          transform-origin: left;
  border-radius: 30px;
}
.hamburger.active span:nth-child(1) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.hamburger.active span:nth-child(2) {
  width: 0;
}
.hamburger.active span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

/* home===============================>> */

.bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-filter: brightness(50%) contrast(1.1) saturate(1.5);
          filter: brightness(50%) contrast(1.1) saturate(1.5);
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.home,
.about,
.service,
.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.home {
  justify-content: end;
  text-align: center;
  text-transform: capitalize;
  margin-right: 5rem;
}

.home h2 {
  font-size: 4rem;
}
.home p {
  letter-spacing: 2px;
}

@media (max-width: 768px) {
  nav {
    padding: 1rem;
  }

  nav .brand {
    font-size: 2.15rem;
    word-spacing: 5px;
  }
  .home {
    margin: 0;
  }
  .home h2 {
    font-size: 2.5rem;
  }
}

